@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;display:flex;justify-content:center;align-items:center;color:#364190}.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.login{width:27em;height:40em;display:flex;justify-content:center;align-items:center;margin-top:5px;position:relative;z-index:99}.login .container{display:flex;justify-content:center;align-items:center;background-color:#fff;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:4px;position:relative;z-index:99;width:100%;height:100%;z-index:99;padding:17px 10px}.login .right-side{display:flex;flex-direction:column;justify-content:center;height:90%;background-color:#364190;width:100%;position:absolute;right:-34%;border-radius:6px;z-index:1;transition:all 400ms ease-in-out;cursor:pointer;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.281)}.login .right-side.right{right:-40%;align-items:flex-end}.login .right-side.right:hover{right:-45%}.login .right-side.left{right:40%;align-items:flex-start}.login .right-side.left:hover{right:45%}.login .right-side .text{font-size:21px;font-weight:500;color:#fff;margin-right:3em;margin-left:3em}

.base-container{width:100%;display:flex;flex-direction:column;align-items:center}.base-container .content{display:flex;flex-direction:column;overflow:hidden}.base-container .content .image{margin-left:10px}.base-container .content .image img{width:100%;height:100%;border-radius:50%;overflow:hidden;-webkit-user-select:none;-ms-user-select:none;user-select:none}.base-container .content .image-reg{width:15em;margin-left:40px;margin-top:82px}.base-container .content .image-reg img{width:150px;height:150px;border-radius:50%;overflow:hidden;-webkit-user-select:none;-ms-user-select:none;user-select:none;box-shadow:0px 0px 3px 1px #0e81ce96}.base-container .content .form{margin-top:2em;display:flex;flex-direction:column;align-items:center}.base-container .content .form .form-group{display:flex;flex-direction:column;align-items:flex-start;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.base-container .content .form .form-group label{font-size:20px}.base-container .footer{margin-top:3em}input{margin-top:6px;min-width:18em;height:37px;padding:0px 10px;font-size:16px;font-family:"Open Sans", sans-serif;background-color:#f3f3f3;border:0;border-radius:4px;margin-bottom:31px;transition:all 250ms ease-in-out}input:hover{background-color:#ffffff;box-shadow:0px 0px 14px 0.3px #0e81ce96}input:focus{outline:none;box-shadow:0px 0px 12px 0.8px #3474dbb2}.from-group-hide input{display:none}

